<template>
    <!-- 首页评论组件 -->
    <div class="ratings" ref="ratings">
        <div class="ratings-content">
            <div class="overview">
                <div class="overview-left border-right">
                    <h1 class="score">{{seller.score}}</h1>
                    <div class="title">综合评分</div>
                    <div class="rank">高于周边商家{{seller.rankRate}}</div>
                </div>
                <div class="overview-right">
                    <div class="score-wrapper">
                        <div class="title">服务态度</div>
                        <star class="star" :size="36" :score="seller.serviceScore"></star>
                        <span class="score">{{seller.serviceScore}}</span>
                    </div>
                    <div class="score-wrapper">
                        <div class="title">商品评分</div>
                        <star class="star" :size="36" :score="seller.foodScore"></star>
                        <span class="score">{{seller.foodScore}}</span>
                    </div>
                    <div class="delivery-wrapper">
                        <span class="title">送达时间 </span>
                        <span class="delivery">{{seller.deliveryTime}}分钟</span>
                    </div>
                </div>
            </div>
            <split></split>
            <ratingselect :selectType="selectTypeAndOnlyContent"
            :desc="desc"
            :ratings="ratings"
            @ratingtypeSelect="ratingtypeSelect"
            @contentToggle="contentToggle">
            </ratingselect>
            <div class="rating-wrapper">
                <ul>
                    <li v-for="rating in ratings" v-show="needShow(rating.rateType, rating.text)" class="rating-item border-bottom"
                    :key="rating.id">
                        <div class="avatar">
                            <img width="28" height="28" :src="rating.avatar">
                        </div>
                        <div class="content">
                            <h1 class="name">{{rating.username}}</h1>
                            <div class="star-wrapper">
                                <star :size="24" :score="rating.score"></star>
                                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
                            </div>
                            <p class="text">{{rating.text}}</p>
                            <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                                <span class="icon-thumb_up"></span>
                                <span class="item" v-for="item in rating.recommend" :key="item.id">{{item}}</span>
                            </div>
                            <div class="time">
                                {{rating.rateTime | formatDate}}
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import axios from 'axios'
import star from '../start/start'
import ratingselect from '../ratingselect/ratingselect'
import split from '../split/split'
import {formatDate} from '../../common/js/date.js' // 为什么会有{}，因为他是export function出来的
// 而不带{}，都是vue组件export default出来的

const ALL = 2
const ERR_OK = 0

export default {
  filters: {
    formatDate (time) {
      let date = new Date(time)
      return formatDate(date, 'yyyy-MM-dd hh:mm')
    }
  },
  components: {
    star,
    split,
    ratingselect
  },
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
      ratings: [],
      selectTypeAndOnlyContent: {
        selectType: ALL,
        onlyContent: false
      },
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    }
  },
  created () {
    axios.get('/api/ratings')
      .then((res) => {
        this.res = res.data
        if (this.res.errno === ERR_OK) {
          this.ratings = this.res.data
          console.log('-------', this.ratings)
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.ratings, {
              click: true
            })
          })
        }
      })
  },
  methods: {
    ratingtypeSelect (type) {
      this.selectTypeAndOnlyContent.selectType = type
      this.$nextTick(() => {
        this.scroll.refresh() // 异步更新DOM 刷新better scroll
      })
    },

    contentToggle (content) {
      this.selectTypeAndOnlyContent.onlyContent = content
      this.$nextTick(() => {
        this.scroll.refresh()
      })
    },
    needShow (type, text) {
      if (this.selectTypeAndOnlyContent.onlyContent && !text) {
        return false // 只看有内容的评价，并且text无，则不显示
      }
      if (this.selectTypeAndOnlyContent.selectType === ALL) {
        return true // 查看全部，则显示
      } else {
        return type === this.selectTypeAndOnlyContent.selectType // 否则显示你多选择的对应方式（全部、推荐、吐槽）
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
    .ratings
      position absolute
      top 174px
      bottom 0
      left 0
      width 100%
      overflow hidden
      .overview
        display flex
        padding 18px 0
        .overview-left
          padding 6px 0
          flex 0 0 137px
          width 137px
          text-align center
          @media only screen and (max-width: 320px)
            flex 0 0 120px
            width 120px
          &.border-right::before
            border-color rgb(7,17,27)
          .score
            margin-bottom 6px
            line-height 28px
            font-size 24px
            color rgb(255,153,0)
          .title
            margin-bottom 8px
            line-height 12px
            font-size 12px
            color rgb(7,17,27)
          .rank
            line-height 10px
            font-size 10px
            color rgb(147,153,159)
        .overview-right
          flex 1
          padding 6px 0 6px 24px
          @media only screen and (max-width: 320px)
            padding-left 6px
            .star
              margin 0!important
          .score-wrapper
            line-height 18px
            margin-bottom 8px
            font-size 0
            .title
              line-height 18px
              display inline-block
              vertical-align top
              font-size 12px
              color rgb(7,17,27)
            .star
              display inline-block
              margin 0 12px
              vertical-align top
            .score
              display inline-block
              vertical-align top
              font-size 12px
              color rgb(255,153,0)
          .delivery-wrapper
            font-size 0
            .title
              line-height 18px
              font-size 12px
              color rgb(7,17,27)
            .delivery
              margin-left 12px
              font-size 12px
              color rgb(147,153,159)
      .rating-wrapper
        padding 0 18px
        .rating-item
          display flex
          padding 18px 0
          &.border-bottom::before
            border-color rgba(7, 17, 27, 0.1)
          .avatar
            flex 0 0 28px
            width 28px
            margin-right 12px
            img
              border-radius 50%
          .content
            position relative
            flex 1
            .name
              margin-bottom 4px
              line-height 12px
              color rgb(7,17,27)
            .star-wrapper
              margin-bottom 6px
              font-size 0
              .star
                display inline-block
                margin-right 6px
                vertical-align top
              .delivery
                display inline-block
                vertical-align top
                line-height 12px
                font-size 10px
                color rgb(147, 153, 159)
            .text
              margin-bottom 8px
              line-height 18px
              color rgb(7, 17, 27)
              font-size 12px
            .recommend
              line-height 16px
              font-size 0
              .icon-thumb_up, .item
                display inline-block
                margin 0 8px 4px 0
                font-size 9px
              .icon-thumb_up
                color rgb(0, 160, 220)
              .item
                padding 0 6px
                border 1px solid rgba(7, 17, 27, 0.1)
                border-radius 1px
                color rgb(147, 153, 159)
                background #fff
            .time
              position absolute
              top 0
              right 0
              line-height 12px
              font-size 10px
              color rgb(147, 153, 159)
</style>



