<template>
    <div class="seller" ref="seller">
        <div class="seller-content">
            <div class="overview">
                <h1 class="title">{{seller.name}}</h1>
                <div class="desc border-bottom">
                    <star :size="36" :score="seller.score"></star>
                    <span class="text">({{seller.ratingCount}})</span>
                    <span class="text">月售{{seller.sellCount}}单</span>
                </div>
                <ul class="remark">
                    <li class="block border-right">
                        <h2>起送价</h2>
                        <div class="content">
                            <span class="stress">{{seller.minPrice}}</span>元
                        </div>
                    </li>
                    <li class="block border-right">
                        <h2>商家配送</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryPrice}}</span>元
                        </div>
                    </li>
                    <li class="block border-right">
                        <h2>平均配送时间</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryTime}}</span>分钟
                        </div>
                    </li>
                </ul>
                <div class="favorite" @click="toggleFavorite">
                  <span class="icon-favorite" :class="{'active':favorite}"></span>
                  <span class="text">{{favoriteText}}</span>
                </div>
            </div>
            <split></split>
            <div class="bulletin">
                <h1 class="title">公告与活动</h1>
                <div class="content-wrapper border-bottom">
                    <p class="content">{{seller.bulletin}}</p>
                </div>
                <ul v-if="seller.supports" class="supports">
                    <li class="support-item border" v-for="(item,index) in seller.supports" :key="item.id">
                        <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                        <span class="text">{{seller.supports[index].description}}</span>
                    </li>
                </ul>
            </div>
            <split></split>
            <div class="pics">
                <h1 class="title">商家实景</h1>
                    <div class="pic-wrapper" ref="picWrapper">
                        <ul class="pic-list" ref="picList">
                            <li class="pic-item" v-for="pic in seller.pics" :key="pic.id">
                                <img :src="pic" width="120" height="90">
                            </li>
                        </ul>
                    </div>
            </div>
            <split></split>
            <div class="info">
                <h1 class="title border-bottom">商家信息</h1>
                <ul>
                    <li class="info-item border-bottom" v-for="info in seller.infos" :key="info.id">{{info}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import star from '../start/start'
import split from '../split/split'
import {saveToLocal, loadFromLocal} from '../../common/js/store.js'
export default {
  data () {
    return {
      favorite: (() => {
        return loadFromLocal(this.seller.id, 'favorite', false)
      })()
    }
  },
  computed: {
    favoriteText () {
      return this.favorite ? '已收藏' : '未收藏'
    }
  },
  components: {
    star,
    split
  },
  props: {
    seller: {
      type: Object
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    // 前面的小图标（优惠，折扣，特殊，发票，保证），类名不同设置不同的小图标
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.seller, {
      click: true
    })
    if (this.seller.pics) {
      console.log(this.$refs.seller)
      let picWidth = 120
      let margin = 6
      let width = (picWidth + margin) * this.seller.pics.length
      this.$refs.picList.style.width = width + 'px'
      // console.log(this.$refs.picList[0].offsetWidth)
      this.$nextTick(() => {
        this.picScroll = new BScroll(this.$refs.picWrapper, {
          scrollX: true,
          eventPassthrough: 'vertical'
        })
      })
    }
  },
  methods: {
    toggleFavorite () {
      this.favorite = !this.favorite
      saveToLocal(this.seller.id, 'favorite', this.favorite)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/common/stylus/mixin.styl'
    .seller
      position absolute
      top 174px
      bottom 0
      width 100%
      left 0
      width 100%
      overflow hidden
      .overview
        position relative
        padding 18px
        .title
          margin-bottom 8px
          line-height 14px
          color rgb(7,17,27)
          font-size 14px
        .desc
          padding-bottom 18px
          font-size 0
          &.border-bottom::before
            border-color rgba(7,17,27,0.1)
          .star
            display inline-block
            margin-right 8px
            vertical-align top
          .text
            display inline-block
            margin-right 12px
            line-height 18px
            vertical-align top
            font-size 10px
        .remark
          display flex
          padding-top 18px
          .block
            flex 1
            text-align center
            &.border-right::before
              border-color rgba(7,17,27,0.1)
            &:last-child.border-right::before
              border-right none
            h2
              margin-bottom 4px
              line-height 10px
              font-size 10px
              color rgb(147,153,159)
            .content
              line-height 24px
              font-size 10px
              color rgb(7,17,27)
              .stress
                font-size 24px
        .favorite
          position absolute
          right 18px
          top 18px
          text-align center
          .icon-favorite
            display block
            margin-bottom 4px
            line-height 24px
            font-size 24px
            color #d4d6d9
            &.active
              color rgb(240,20,20)
          .text
            font-size 14px
            line-height 10px
            color: rgb(77, 85, 93)
      .bulletin
        padding 18px 18px 0 18px
        .title
          margin-bottom 8px
          line-height 14px
          color rgb(7,17,27)
          font-size 14px
        .content-wrapper
          padding 0 12px 16px 12px
          &.border-bottom:before
            border-color rgba(7,17,27,0.1)
          .content
            line-height 24px
            font-size 12px
            color rgb(240,20,20)
        .supports
          .support-item
            padding 16px 12px
            &.border::before
              border-color rgba(7,17,27,0.1)
            .icon
              display inline-block
              vertical-align top
              width 16px
              height 16px
              margin-right 4px
              background-size: 16px 16px
              background-repeat no-repeat
              &.decrease
                bg-image('decrease_4')
              &.discount
                bg-image('discount_4')
              &.guarantee
                bg-image('guarantee_4')
              &.invoice
                bg-image('invoice_4')
              &.special
                bg-image('special_4')
            .text
              line-height 16px
              font-size 12px
              color rgb(7, 17, 27)

      .pics
        padding 18px
        .title
          margin-bottom 12px
          line-height 14px
          color rgb(7,17,27)
          font-size 14px
        .pic-wrapper
          width 100%
          overflow hidden
          white-space nowrap
          .pic-list
            font-size 0
            .pic-item
              display inline-block
              margin-right 6px
              width 120px
              height 90px
              &:last-child
                margin 0
      .info
        padding 18px 18px 0 18px
        .title
          padding-bottom 12px
          font-size 14px
          &.border-bottom::before
            border-color rgba(7,17,27,0.1)
        .info-item
          padding 16px 12px
          line-height 16px
          font-size 12px
          &.border-bottom::before
            border-color rgba(7,17,27,0.1)
          &:last-child
            border none
</style>



